module.exports = function (SmartlingBaseOptions) {
  class SmartlingUploadOptions extends SmartlingBaseOptions {
    constructor(props) {
      super(props);

      this.fileUri = props.fileUri;
      this.fileType = props.fileType || 'json';
      this.contentType = props.contentType || 'application/json';
      this.content = props.content;
    }
  }

  return SmartlingUploadOptions;
};
